r {\n    z-index: -1;\n    content: \"\";\n    width: 100%;\n    height:100%;\n    position: absolute;\n    right:0;\n    top:0;\n    background-color: cornflowerblue;\n}\n\n#projects .project:hover{\n    background-color: var(--light-blue);\n}\n\n#projects .project:not(.active):hover::after{\n    content: \"\";\n    width: 12px;\n    height:100%;\n    position: absolute;\n    right:0;\n    top:0;\n    background-color: cornflowerblue;\n}\n\n#projects .name{\n    line-height: 2rem;\n    flex: 1;\n}\n\n\n#projects .delete{\n    color: palevioletred;\n    cursor: pointer;\n    font-family: Arial, Helvetica, sans-serif;\n    \n    margin-right: 10px;\n    opacity: 0;\n    transition: 200ms, margin 400ms ease-out;\n}\n\n#projects .active .delete{\n    color: white;\n}\n\n#projects .project:hover .delete{\n    opacity: 0.6;\n}\n\n#projects .project:hover .delete:hover{\n    opacity: 1;\n}\n\n#projects .edit-form input{\n    color: white;\n    background-color: cornflowerblue;\n    font-style: italic;\n}\n\n#projects input {\n    padding: 5px 10px;\n    box-sizing: border-box;\n    appearance: none;\n    height: 60px;\n    width: 100%;\n\n    color: rgb(160, 160, 160);\n    box-shadow: inset 1px 0px 4px rgba(0,0,0,0.5);\n    font-size: 0.9rem;\n    outline: none;\n    border: none;\n}\n\n#projects input::placeholder {\n    font-style: italic;\n    color: rgb(196, 193, 193);\n  }\n\nmain {\n    overflow: auto;\n    padding: 30px 30px;\n    display: grid;\n    grid-template-columns: repeat(auto-fit, 350px);\n    grid-auto-rows: max-content;\n    gap: 30px;\n}\n\n.card {\n    position: relative;\n    box-sizing: border-box;\n    padding: 20px 30px;\n    height: 300px;\n    background-color: white;\n    box-shadow: 2px 2px 9px rgba(0,0,0,0.5);\n\n    display: flex;\n    flex-direction: column;\n\n    transition: 300ms;\n    cursor: pointer;\n}\n\n.card::after{\n    content: \"\";\n    position: absolute;\n    top: 0;\n    right: 0;\n    width:10px;\n    height: 100%;\n    background-color: cornflowerblue;\n}\n\n.card .title{\n    font-size: 1.3rem;\n    width: fit-content;\n    margin-bottom: 5px;\n    overflow: hidden;\n\n    display: -webkit-box;\n    -webkit-line-clamp: 2;\n    -webkit-box-orient: vertical;\n    overflow: hidden;\n    text-overflow: ellipsis;\n\n    cursor: text;\n}\n\n.card .descr{\n    margin-bottom: 10px;\n    width: fit-content;\n\n    font-size: 1rem;\n    line-height: 1em;\n    height: 2em;\n\n    display: -webkit-box;\n    -webkit-line-clamp: 2;\n    -webkit-box-orient: vertical;\n    overflow: hidden;\n    text-overflow: ellipsis;\n\n    cursor: text;\n}\n\n.card .descr:empty::after{\n    content: 'Description';\n    cursor: text;\n    font-style:italic;\n    color:rgb(160, 160, 160)\n}\n\n.card .checklist-title {\n    font-size: 0.9rem;\n    margin-bottom: 5px;\n    cursor: text;\n}\n\n.card .checklist-container{\n    display: flex;\n    flex-direction: column;\n    gap: 5px;\n    overflow-y: auto;\n}\n\n.card:not(#new-todo,.edit-form) .checklist-container{\n    pointer-events: none;\n}\n\n.card .checklist-item{\n    color: grey;\n    font-size: 0.9rem;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n    list-style: square; \n    width: min(fit-content,100%);\n    \n    cursor: text;\n    pointer-events: all;\n}\n\n.card .checklist-container:empty{\n    width: fit-content;\n}\n\n.card .checklist-container:empty::after {\n    margin-top: 10px;\n    content: \"Add Tasks\";\n    font-style: italic;\n    color:rgb(160, 160, 160);\n    font-size: 0.8rem;\n    cursor: text;\n}\n\n.card .button-container{\n    margin-top: auto;\n\n    display: flex;\n    justify-content: space-evenly;\n\n}\n\nbutton {\n    appearance: none;\n    border: none;\n}\n\n.button{\n    box-sizing: border-box;\n    padding: 5px;\n    font-size: 0.9rem;\n    width: 80px;\n    border-radius: 6px;\n    text-align: center;\n    color: white;\n    background-color: cornflowerblue;\n    transition: 300ms ease-in-o