 rgb(100, 100, 100);\n    outline: none !important;\n    border-radius: 5px;\n    border: 0.5px rgb(180, 180, 180) solid;\n}\n\n#todo-details #more-details{\n    height: 50%;\n    display: flex;\n    flex:1;\n}\n\n#todo-details h3{\n    font-size: 1rem;\n}\n\n#todo-details .checklist-container{\n    font-size: 1.2rem;\n    width: 100%;\n    display: flex;\n    flex-direction: column;\n    gap: 10px;\n}\n\n#todo-details .checklist-container ul{\n    display: flex;\n    flex-direction: column;\n    gap: 15px;\n}\n\n#todo-details .checklist-container input{\n    line-height: 30px;\n    padding-left: 10px;\n    font-size: 0.9rem;\n}\n\n#todo-details .dates{\n    flex:1;\n    display: flex;\n    gap: 60px;\n    align-items: center;\n    justify-content: center;\n}\n\n#todo-details .date-container{\n    display: flex;\n    align-items: center;\n    gap: 10px;\n}\n\n#todo-details .date-container input{\n    font-size: 1rem;\n}\n\n#todo-details .date-type{\n    text-align: center;\n}\n\n#todo-details .button-container{\n    display: flex;\n    justify-content: center;\n    gap: 110px;\n    position: relative\n}\n\n#todo-details.done .done-toggle{\n    background-color: green;\n}\n\n#todo-details .button{\n    width: 110px;\n}\n\n#todo-details .save.button{\n    color: white;\n}\n\n#checklist {\n    overflow: auto;\n    height: 10rem;\n}\n\n#checklist .checklist-item {\n    display: flex;\n    align-items: center;\n}\n\n#checklist input {\n    box-sizing: border-box;\n    width:100%;\n    height: 40px;\n    margin-right: -50px;\n}\n\n#checklist .done input {\n    text-decoration: line-through;\n    background-color: var(--light-green);\n}\n\n#checklist .item-button-container {\n    display: flex;\n    gap: 5px;\n    user-select: none;\n}\n\n#checklist .item-button {\n    cursor: pointer;\n    transition: 200ms ease-out;\n}\n\n#todo-details #checklist .done-toggle {\n    font-weight: 600;\n    background-color: transparent;\n    color: grey;\n}\n\n#todo-details #checklist .do