ue;\n}\n\n#inspiration {\n    font-size: 1.5rem;\n}\n\n#sidebar {\n    overflow: auto;\n    grid-area: sidebar;\n    box-shadow: 0 0 8px rgba(0,0,0,0.5);\n}\n\n#projects {\n    display: flex;\n    flex-direction: column;\n}\n\n#projects .project{\n    box-sizing: border-box;\n    height: 60px;\n    padding: 5px 10px;\n    position: relative;\n\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n\n    color: rgb(100, 100, 100);\n    transition: all 300ms ease-out;\n    background-color: white;\n    box-shadow: inset 1px 0px 4px rgba(0,0,0,0.3);\n    outline: solid 0.5px rgb(213, 213, 213);\n    cursor: pointer;\n}\n\n#projects .project.active {\n    z-index: 1;\n    color: white;\n    position: relative;\n    padding-left: 9px;\n    box-shadow: inset 1px 0px 2px rgba(0,0,0,0.5);\n}\n\n#projects .project::after{\n    content: \"\";\n    width: 6px;\n    height:100%;\n    position: absolute;\n    right:0;\n    top:0;\n\n    transition: 300ms ease-out;\n    background-color: cornflowerblue; \n}\n\n#projects .project.active::after {\n    z-index: -1;\n    content: \"\";\n    width: 100%;\n    height:100%;\n    position: absolute;\n    right:0;\n    top:0;\n    background-color: cornflowerblue;\n}\n\n#projects .project:hover{\n    background-color: var(--light-blue);\n}\n\n#projects .project:not(.active):hover::after{\n    content: \"\";\n    width: 12px;\n    height:100%;\n    position: absolute;\n    right:0;\n    top:0;\n    background-color: cornflowerblue;\n}\n\n#projects .name{\n    line-height: 2rem;\n    flex: 1;\n}\n\n\n#projects .delete{\n    color: palevioletred;\n    cursor: pointer;\n    font-family: Arial, Helvetica, sans-serif;\n    \n    margin-right: 10px;\n    opacity: 0;\n    transition: 200ms, margin 400ms ease-out;\n}\n\n#projects .active .delete{\n    color: white;\n}\n\n#projects .project:hover .delete{\n    opacity: 0.6;\n}\n\n#projects .project:hover .delete:hover{\n    opacity: 1;\n}\n\n#projects .edit-form input{\n    color: white;\n    background-color: cornflowerblue;\n    font-style: italic;\n}\n\n#projects input {\n    padding: 5px 10px;\n    box-sizing: border-box;\n    appearance: none;\n    height: 60px;\n    width: 100%;\n\n    color: rgb(160, 160, 160);\n    box-shadow: inset 1px 0px 4px rgba(0,0,0,0.5);\n    font-size: 0.9rem;\n    outline: none;\n    border: none;\n}\n\n#projects input::placeholder {\n    font-style: italic;\n    color: rgb(196, 193, 193);\n  }\n\nmain {\n    overflow: auto;\n    padding: 30px 30px;\n    display: grid;\n    grid-template-columns: repeat(auto-fit, 350px);\n    grid-auto-rows: max-content;\n    gap: 30px;\n}\n\n.card {\n    position: relative;\n    box-sizing: border-box;\n    padding: 20px 30px;\n    height: 300px;\n    background-color: white;\n    box-shadow: 2px 2px 9px rgba(0,0,0,0.5);\n\n    display: flex;\n    flex-direction: column;\n\n    transition: 300ms;\n    cursor: pointer;\n}\n\n.card::after{\n    content: \"\";\n    position: absolute;\n    top: 0;\n    right: 0;\n    width:10px;\n    height: 100%;\n    background-color: cornflowerblue;\n}\n\n.card .title{\n    font-size: 1.3rem;\n    width: fit-content;\n    margin-bottom: 5px;\n    overflow: hidden;\n\n    display: -webkit-box;\n    -webkit-line-clamp: 2;\n    -webkit-box-orient: vertical;\n    overflow: hidden;\n    text-overflow: ellipsis;\n\n    cursor: text;\n}\n\n.card .descr{\n    margin-bottom: 10px;\n    width: fit-content;\n\n    font-size: 1rem;\n    line-height: 1em;\n    height: 2em;\n\n    display: -webkit-box;\n    -webkit-line-clamp: 2;\n    -webkit-box-orient: vertical;\n    overflow: hidden;\n    text-overflow: ellipsis;\n\n    cursor: text;\n}\n\n.card .descr:empty::after{\n    content: 'Description';\n    cursor: text;\n    font-style:italic;\n    color:rgb(160, 160, 160)\n}\n\n.card .checklist-title {\n    font-size: 0.9rem;\n    margin-bottom: 5px;\n    cursor: text;\n}\n\n.card .checklist-container{\n    display: flex;\n    flex-direction: column;\n    gap: 5px;\n    overflow-y: auto;\n}\n\n.card:not(#new-todo,.edit-form) .checklist-container{\n    pointer-events: none;\n}\n\n.card .checklist-item{\n    color: grey;\n    font-size: 0.9rem;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n    list-style: square; \n    width: min(fit-content,100%);\n    \n    cursor: text;\n    pointer-events: all;\n}\n\n.card .checklist-container:empty{\n    width: fit-content;\n}\n\n.card .checklist-container:empty::after {\n    margin-top: 10px;\n    content: \"Add Tasks\";\n    font-style: italic;\n    color:rgb(160, 160, 160);\n    font-size: 0.8rem;\n    cursor: text;\n}\n\n.card .button-container{\n    margin-top: auto;\n\n    display: flex;\n    justify-content: space-evenly;\n\n}\n\nbutton {\n    appearance: none;\n    border: none;\n}\n\n.button{\n    box-sizing: border-box;\n    padding: 5px;\n    font-size: 0.9rem;\n    width: 80px;\n    border-radius: 6px;\n    text-align: center;\n    color: white;\n    background-color: cornflowerblue;\n    transition: 300ms ease-in-out;\n    cursor: pointer;\n    user-select: none;\n}\n\n.button:hover{\n    background-color: rgb(90, 131, 207);\n}\n\n.button.done-toggle{\n    background-color: cornflowerblue;\n}\n\n.button.done-toggle:hover{\n    background-color: green;\n}\n\n.card.done .done-toggle{\n    background-color: green;\n}\n\n.button.delete{\n    background-color: crimson;\n}\n\n.button.delete:hover{\n    background-color: brown;\n}\n\n#reset-todo-inputs{\n    background-color: rgb(170, 170, 170);\n}\n\n#reset-todo-inputs:hover{\n    background-color: grey;\n}\n\n\n.card.done {\n    background-color: var(--light-green);\n}\n\n.card.done .title {\n    text-decoration: line-through;\n    color:rgb(80, 80, 80)\n}\n\n.card input,textarea {\n    resize: none;\n    padding: 4px 5px;\n    outline: none;\n    border: 0.3px rgb(208, 208, 208) solid;\n    border-radius: 5px;\n    \n    color: rgb(100, 100, 100);\n    font-style: italic;\n}\n\n.card input.title{\n    width: auto;\n}\n\n.card textarea.descr{\n    font-size: 0.8rem;\n    width: auto;\n}\n\n#todo-background{\n    z-index: 1;\n    height: 100vh;\n    width: 100vw;\n    background-color: rgba(0,0,0,0.5);\n    position: absolute;\n}\n\n#todo-details{\n    padding: 30px 50px;\n    position: absolute;\n    top: 100px;\n    left: 200px;\n    display: flex;\n    flex-direction: column;\n    gap: 20px;\n    background-color: white;\n    height: 60vh;\n    width: 70vw;\n\n    transition: 300ms;\n}\n\n#todo-details.done{\n    background-color: var(--lighter-green);\n}\n\n#todo-details :is(input,textarea){\n    transition-duration: 300ms;\n    background-color: white;\n}\n\n#todo-details.done :is(input,textarea){\n    background-color: var(--light-green);\n}\n\n#todo-details .title{\n    font-size: 2rem;\n    padding-left: 10px;\n}\n\n#todo-details textarea.descr{\n    padding-left: 10px;\n    font-style: normal;\n    font-size: 1.1rem;\n    height: 20%;\n    resize: none;\n}\n\n#todo-details input,textarea{\n    appearance: none;\n    color: rgb(100, 100, 100);\n    outline: none !important;\n    border-radius: 5px;\n    border: 0.5px rgb(180, 180, 180) solid;\n}\n\n#todo-details #more-details{\n    height: 50%;\n    display: flex;\n    flex:1;\n}\n\n#todo-details h3{\n    font-size: 1rem;\n}\n\n#todo-details .checklist-container{\n    font-size: 1.2rem;\n    width: 100%;\n    display: flex;\n    flex-direction: column;\n    gap: 10px;\n}\n\n#todo-details .checklist-container ul{\n    display: flex;\n    flex-direction: column;\n    gap: 15px;\n}\n\n#todo-details .checklist-container input{\n    line-height: 30px;\n    padding-left: 10px;\n    font-size: 0.9rem;\n}\n\n#todo-details .dates{\n    flex:1;\n    display: flex;\n    gap: 60px;\n    align-items: center;\n    justify-content: center;\n}\n\n#todo-details .date-container{\n    display: flex;\n    align-items: center;\n    gap: 10px;\n}\n\n#todo-details .date-container input{\n    font-size: 1rem;\n}\n\n#todo-details .date-type{\n    text-align: center;\n}\n\n#todo-details .button-container{\n    display: flex;\n    justify-content: center;\n    gap: 110px;\n    position: relative\n}\n\n#todo-details.done .done-toggle{\n    background-color: green;\n}\n\n#todo-details .button{\n    width: 110px;\n}\n\n#todo-details .save.button{\n    color: white;\n}\n\n#checklist {\n    overflow: auto;\n    height: 10rem;\n}\n\n#checklist .checklist-item {\n    display: flex;\n    align-items: center;\n}\n\n#checklist input {\n    box-sizing: border-box;\n    width:100%;\n    height: 40px;\n    margin-right: -50px;\n}\n\n#checklist .done input {\n    text-decoration: line-through;\n    background-color: var(--light-green);\n}\n\n#checklist .item-button-container {\n    display: flex;\n    gap: 5px;\n    user-select: none;\n}\n\n#checklist .item-button {\n    cursor: pointer;\n    transition: 200ms ease-out;\n}\n\n#todo-details #checklist .done-toggle {\n    font-weight: 600;\n    background-color: transparent;\n    color: grey;\n}\n\n#todo-details #checklist .done-toggle:hover {\n    color: green;\n}\n\n#todo-details #checklist .done .done-toggle {\n    background-color: transparent;\n    color: green;\n}\n\n#todo-details #checklist .delete{\n    color: crimson;\n}\n\n#todo-details #checklist .delete:hover{\n    color: brown;\n}\n\n#new-app {\n    color: cornflowerblue;\n    width: 500px;\n    font-size: 1.7rem;\n    margin-top: -14px;\n    font-style: italic;\n}\n"],"sourceRoot":""}]);
